<template>
    <div id="desbravando" class="wrapper">

        <div class="static-container">
            <h1 class="title">
                galery<br v-if="viewport.is568">{<span class="params">'Foto'</span>}
            </h1>

            <div class="std initial-fold">
                <p class="-purple -full" v-if="viewport.is568">
                    // Kumpulan Foto<br>
                </p>
                <p class="-purple -full" v-else>
                    // Kumpulan Foto<br>
                </p>

                <h2 class="subtitle">
                    Kumpulan Jepretan Kamera
                </h2>
                <p class="-gray">
                    ini adalah kumpulan beberapa foto yang tak jepret sendiri 
                </p>
            </div>

            <div class="project-start"></div>


            <div class="std">

                <h3 class="-red">// Just Foto</h3>
                

            </div>

            <div class="gallery-inspiration">
                <ul>
                    <li class="f1">
                        <figure>
                            <img src="@/assets/pepe/sampah-1.jpg" alt="">
                            <figcaption>foto tersebut diambil  <i>"Pantai Amal"</i> dengan keadaan sampah yang menumpuk
                            at Tarakan.
                            </figcaption>
                        </figure>
                    </li>
                    <li class="f2">
                        <figure>
                            <img src="@/assets/pepe/sampah-2.jpg" alt="">
                            <figcaption>foto tersebut diambil  <i>"Pantai Amal"</i> dengan keadaan sampah yang menumpuk
                            at Tarakan.
                            </figcaption>
                        </figure>
                    </li>
                    <li class="f3">
                        <figure>
                            <img src="@/assets/pepe/bermain-1.jpg" alt="">
                            <figcaption>Anak-anak bermain kelereng, Foto tersebut diambil di <i>"Perkampuangan Juata"</i> at Tarakan.
                            </figcaption>
                        </figure>
                    </li>
                    <li class="f4">
                        <figure>
                            <img src="@/assets/pepe/bermain-2.jpg" alt="">
                            <figcaption>Anak-anak bermain petak umpet di kapal, Foto tersebut diambil di <i>"Pantai Amal"</i> at Tarakan.
                            </figcaption>
                        </figure>
                    </li>
                    <li class="f5">
                        <figure>
                            <img src="@/assets/pepe/model-1.jpg" alt="">
                            <figcaption>Merah tak Sengaja Foto tersebut diambil di <i>"Grand Tarakan Mall"</i> at Tarakan.
                            </figcaption>
                        </figure>
                    </li>
                    <li class="f6">
                        <figure>
                            <img src="@/assets/pepe/model-2.jpg" alt="">
                            <figcaption>litte Doll Foto tersebut diambil di <i>"Grand Tarakan Mall"</i> at Tarakan.
                            </figcaption>
                        </figure>
                    </li>
                    <li class="f7">
                        <figure>
                            <img src="@/assets/pepe/model-3.jpg" alt="">
                            <figcaption>Smokei Man Foto tersebut diambil di <i>"Perum Pepabri"</i> at Tarakan.
                            </figcaption>
                        </figure>
                    </li>
                </ul>
            </div>

         
            <nav class="work-nav -pepe">
                <h3>// More...</h3>
                <ul>
                    <li class="prev">
                        <router-link to="/work/diary" title="Diary">
                            <svg class="nav-ico -prev" viewBox="0 0 16 16">
                                <path d="M5,1h10v10 M5.5,10.5l0.8-0.8"/>
                            </svg>
                            ...Diary
                        </router-link>
                    </li>
                    <li class="next">
                        <router-link to="/work/karya" title="Karya">
                            Karya...
                            <svg class="nav-ico -next" viewBox="0 0 16 16">
                                <path d="M5,1h10v10 M5.5,10.5l0.8-0.8"/>
                            </svg>
                        </router-link>
                    </li>
                </ul>
            </nav>

        </div>

    </div>
</template>

<script>
    // styles
    import '@/styles/work.scss'
    import '@/styles/work/desbravando.scss'
    // ScrollMagic
    import * as ScrollMagic from "scrollmagic";

    export default {
        name: 'desbravando',
        props: {
            viewport: Object,
        },
        data() {
            return {
                intro: new TimelineMax(),
                scroller: new ScrollMagic.Controller(),
            }
        },
        methods: {},
        mounted() {

            /**
             * @desc
             * Intro scene
             */
            this.intro
                .clear(true)
                .addLabel('enter', 1)
                .from('.title', 2, {
                    autoAlpha: 0,
                    rotationX: 20,
                    transformOrigin: '50% 50% -100px',
                    ease: Power3.easeOut,
                }, 'enter')
                .from('.std', 2, {
                    autoAlpha: 0,
                    x: -32,
                    ease: Power3.easeOut,
                }, 'enter+=1.5');

            /**
             * @desc
             * bg color scene
             */
            let sceneBg = new ScrollMagic.Scene({
                triggerElement: '.project-start',
                offset: this.viewport.h / 4,
                duration: document.body.offsetHeight
            })
                .addTo(this.scroller)
                .reverse(true);
            sceneBg
                .on('enter', (e) => {
                    document.body.classList.add('-desbravando-bg');
                })
                .on('leave', (e) => {
                    if (e.scrollDirection === 'REVERSE') {
                        document.body.classList.remove('-desbravando-bg');
                    }
                });

            /**
             * @desc
             * cd disc scene
             */
            let tlDisc = new TimelineMax();
            if (this.viewport.is768) {
                // 768 screen animation
                tlDisc
                    .to('.cd-case .case', 4, {
                        bezier: {
                            curviness: .5,
                            values: [
                                {xPercent: -40,},
                                {xPercent: -10,}
                            ],
                            autoRotate: false
                        },
                    })
                    .to('.cd-case .disc', 4, {
                        bezier: {
                            curviness: 1.25,
                            values: [
                                {xPercent: 60, rotation: 30, zIndex: 2},
                                {xPercent: 20, rotation: 10, zIndex: 10}
                            ],
                            autoRotate: false
                        },
                    }, 0)
                    .to('.cd-case .disc-shadow', 4, {
                        bezier: {
                            curviness: .5,
                            values: [
                                {xPercent: 60},
                                {xPercent: 20}
                            ],
                            autoRotate: false
                        },
                    }, 0);
            } else {
                // default screen animation
                tlDisc
                    .to('.cd-case .case', 4, {
                        xPercent: -20,
                        ease: Power3.easeOut
                    })
                    .fromTo('.cd-case .disc', 4, {
                        rotation: -180,
                    }, {
                        xPercent: 60,
                        rotation: 10,
                        ease: Power3.easeOut
                    }, 0)
                    .to('.cd-case .disc-shadow', 4, {
                        xPercent: 60,
                        ease: Power3.easeOut
                    }, 0);
            }

            let sceneDisc = new ScrollMagic.Scene({
                triggerElement: '.project-start',
                offset: -this.viewport.h / 4,
                duration: this.viewport.h
            })
                .setTween(tlDisc)
                .addTo(this.scroller)
                .reverse(true);

            /**
             * @desc
             * cd gallery scene
             */
            let tlGallery = new TimelineMax();

            tlGallery
                .staggerTo('.gallery-inspiration li', 4, {
                    yPercent: -40,
                    stagger: .5
                });

            let sceneGallery = new ScrollMagic.Scene({
                triggerElement: '.gallery-inspiration',
                offset: -this.viewport.h / 4,
                duration: this.viewport.h * 4
            })
                .setTween(tlGallery)
                .addTo(this.scroller)
                .reverse(true);

            /**
             * @desc
             * cd logo scene
             */
            let tlLogo = new TimelineMax();

            tlLogo
                .staggerTo('.LZeLLPey_0, .LZeLLPey_1, .LZeLLPey_2, .LZeLLPey_3, .LZeLLPey_4', 1, {
                    strokeDashoffset: 0,
                    stagger: 1,
                    ease: Power0.easeNone
                })
                .from('.pepe-logo .text', 5, {
                    autoAlpha: 0,
                    yPercent: -25,
                    ease: Power3.easeOut
                }, 0);

            let sceneLogo = new ScrollMagic.Scene({
                triggerElement: '.pepe-logo',
                offset: -this.viewport.h / 3,
                duration: this.viewport.h
            })
                .setTween(tlLogo)
                .addTo(this.scroller)
                .reverse(true);

        },
        beforeDestroy() {

            this.scroller.destroy();

        }
    };
</script>
